import Siema from 'siema'

export default () => {
  // carousel
  const selector = '.carousel-list'
  const $buttonPrev = document.querySelector('.carousel-buttons .prev')
  const $buttonNext = document.querySelector('.carousel-buttons .next')

  const Carousel = new Siema({
    selector,
    perPage: {
      540: 2,
      860: 3
    },
    loop: true
  })
  
  if ($buttonPrev && $buttonNext) {
    $buttonPrev.addEventListener('click', () => Carousel.prev())
    $buttonNext.addEventListener('click', () => Carousel.next())
  }

  // gallery
  const $galleries = document.querySelectorAll('.carousel-gallery')
  
  $galleries.forEach(($wrap) => {
    const $gallery = $wrap.querySelector('.carousel-gallery-list')
    const $galleryPrev = $wrap.querySelector('.prev')
    const $galleryNext = $wrap.querySelector('.next')

    const Gallery = new Siema({
      selector: $gallery,
      loop: true
    })

    if ($galleryPrev && $galleryNext) {
      $galleryPrev.addEventListener('click', () => Gallery.prev())
      $galleryNext.addEventListener('click', () => Gallery.next())
    }
  });
}
